<template>
  <div >
    <div class="mhead">
      <ul>
        <li>
          <text>自助缴费机</text>
        </li>
      </ul>
    </div>
    <div class="body" style="overflow:auto;">
      <div class="userinfo" style="height: 40%">
        <table width="96%" style="font-size:16px;margin-right:2%;margin-left:2%;" >
          <tr>
            <td colspan="2" class="lab"><span><img src="../image/titleicon01.png" width="26" height="26" alt=""/></span> 档案信息</td>
          </tr>
          <tr>
            <td width="80" align="right">用户编号：</td>
            <td><span>{{user.f_userinfo_code}}</span></td>
          </tr>
          <tr>
            <td align="right">用户名称：</td>
            <td><span>{{user.f_user_name}}</span></td>
          </tr>
          <tr>
            <td align="right">用气性质：</td>
            <td><span>{{user.f_gasproperties}}</span></td>
          </tr>
          <tr>
            <td align="right">用户地址：</td>
            <td ><div style="word-break: break-all;word-wrap: break-word;padding-top: 2%;">{{user.f_address}}</div></td>
          </tr>
          <tr>
            <td align="right">账户余额：</td>
            <td ><span>{{user.f_balance}}</span></td>
          </tr>
          <tr>
            <td align="right">欠费金额：</td>
            <td ><span>{{arrears}}</span></td>
          </tr>
        </table>
      </div>
      <tabset v-ref:tabs :close="false" class="lists" style="margin-top: 20px;height: 50%">
        <tab header=' 充值缴费' class="tabview">
          <div style="width: 100%">
            <table style="width: 100%">
              <tr>
                <td colspan="2">
                  <ul class="selectbuy1" v-for="row in rows" >
                    <li><a href="#" v-bind:class="{ getfocus: row.isActive }"  @click="fastBuy(row.para)" ><span>{{row.para}}</span></a></li>
                  </ul>
                </td>
              </tr>
              <tr>
                <td style="padding-left: 15px;padding-top: 20px;" colspan="2">
                  <span style="font-size: 18px;font-weight: bold;">缴费金额：</span>
                  <input style="width:120px;font-weight: bold;" v-model="collection" :readonly = "isInput" id="money"/>
                </td>
              </tr>
              <tr>
                <td style="padding-left: 15px;padding-top: 20px;" colspan="2">
                  <input type="button" class="btn btn-lg btn-info" style="width:100px; height:40px;font-size: 16px" value="确认订单"
                         @click="generScan()">
                </td>
              </tr>
            </table>
          </div>
        </tab>
        <tab header='历史充值记录' class="tabview">
          <history-sell-list :historysells = "historysells" :selldate = "selldate"></history-sell-list>
        </tab>
      </tabset>
    </div>
    <div class="foot"  @click="back('tomain')">
      <text>返回</text>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'

export default{
  title: '用户信息',
  name : 'userInfo',
  data () {
    return {
      writecards: [],
      historysells: [],
      arrears: 0,
      selldate: '',
      user: {},
      isInput: true,
      rows: [
        {'para': '1000元', isActive: false},
        {'para': '2000元', isActive: false},
        {'para': '3000元', isActive: false},
        {'para': '4000元', isActive: false},
        {'para': '5000元', isActive: false},
        {'para': '其他', isActive: false}
      ],
      collection: 0
    }
  },
  props: ['userInfo', 'cardInfo'],
  methods: {
    back (param) {
      this.$back(param)
    },
    // 查询当前历史收费记录
    getHistorySell() {
      this.$resetpost(Vue.ProxyUrl + '/rs/logic/'+this.$PosUtil.posParam.revenue+'GetHeatOverdue', {data: {f_userinfo_id: `${this.user.f_userinfo_id}`}}, {
        resolveMsg: null,
        rejectMsg: null
      }).then((result) =>{
        // 未写卡记录（欠费记录）
        this.writecards = result.data.orders
        this.historysells = result.data.selles
        console.log('this.writecards:', this.writecards)
        // 计算累计欠费金额
        for (var i = 0; i < this.writecards.length; i++) {
          this.arrears += this.writecards[0].f_oughtfee
        }
      }).catch((err) => {
        console.log('err', err)
        this.$showMessage('系统繁忙，查询历史缴费记录失败，请退出重新读卡！')
      })
    },
    iswritecard(){
      this.$showMessage(msg + '点击"确认"开始写卡', ['confirm', 'cancel']).then((res) => {
        if (res === 'confirm') {
          var stairPrice = this.stairPrices[0]
          stairPrice.writemark = false
        }
      })
    },
    generScan() {
      // 防止多次下单
      if (this.collection < this.arrears) {
        this.$showMessage("输入缴费金额不能小于欠费金额！")
        return
      } else {
        this.user["f_collection"] = this.collection
        this.$goto("choose-pay-way", {userInfo: this.user}, 'self', this.callback)
      }
    },
    fastBuy (para) {
      for (let i = 0; i < this.rows.length; i++) {
        this.rows[i].isActive = false
        if (this.rows[i].para === para) {
          this.rows[i].isActive = true
        }
      }
      if (para === '其他') {
        this.collection = ''
        this.isInput = false
        document.getElementById("money").focus()
      } else {
        this.collection = parseInt(para.substring(0, para.length-1))
      }
    }
  },
  events: {
    'goWritecard' () {
      this.goWrite()
    }
  },
  ready () {
    this.selldate = this.$PosUtil.posParam.sellDate
    this.user = this.userInfo
    // 查询历史购气记录
    this.getHistorySell()
  }
}
</script>



